import React from "react";
import styles from "./styles.module.scss";

const Tags = ({ tags = [] } = {}) => {
  return (
    <div className={`${styles.container}`}>
      {tags.map((t, i) => (
        <span key={i}>
          <a target="_blank" rel="noopener noreferrer" href={t.link || "#"}>
            {t.label}
          </a>
          {i === tags.length - 1 ? "" : ", "}
        </span>
      ))}
    </div>
  );
};

export default Tags;
