import React from "react";
import WorldMap from "../WorldMap";
import TagLine from "../TagLine";
import styles from "./styles.module.scss";

const WorldTravelMap = ({ visitedAreas = [] }) => {
  const sortedVisitedAreas = visitedAreas
    .slice()
    .sort((a, b) => (a.name > b.name ? 1 : -1));
  return (
    <div>
      <WorldMap areas={sortedVisitedAreas} />
      <div className={styles.statsContainer}>
        Total countries visited: {visitedAreas.filter((a) => !a.part).length}
      </div>
      <TagLine
        tags={sortedVisitedAreas.map(({ code, name }) => ({ id: code, name }))}
      />
    </div>
  );
};

export default WorldTravelMap;
