import React from "react";
import WorldMap from "../WorldMap";
import TagLine from "../TagLine";
import styles from "./styles.module.scss";

const WorldTeacherMap = ({ timeline = [] }) => {
  const sortedVisitedAreas = timeline
    .filter(
      ({ tags, location }) =>
        tags &&
        location &&
        location.code &&
        location.name &&
        (tags.indexOf("teaching") !== -1 ||
          tags.indexOf("public speaking") !== -1)
    )
    .map(({ location }) => location)
    .filter(({ code }, i, arr) => arr.findIndex((x) => x.code === code) === i)
    .sort((a, b) => (a.name > b.name ? 1 : -1));
  return (
    <div>
      <div className={styles.heading}>Previous Locations</div>
      <WorldMap areas={sortedVisitedAreas} />
      <TagLine
        tags={sortedVisitedAreas.map(({ code, name }) => ({ id: code, name }))}
      />
    </div>
  );
};

export default WorldTeacherMap;
