const onEmailExpand = () => {
  const { email } = getData();
  reportEvent({
    category: "contactEmail",
    action: "revealed",
    label: window.location.pathname,
    sendOnce: true,
  });
  copyToClipboard(email);
};

const onTelegramExpand = () => {
  const { telegram } = getData();
  reportEvent({
    category: "contactTelegram",
    action: "revealed",
    label: window.location.pathname,
    sendOnce: true,
  });
  window.open(`https://t.me/${telegram}`, "_blank"); // Must be on click event
};

const setUrlForSlideNumber = (slide, clear = false) => {
  const { pages } = getData();
  const slides = Object.keys(pages).length;
  slide = (slides + slide) % slides;
  setCurrentUrlReplacing({
    pathname: pages[slide].path || "/",
    search: clear ? null : {},
    searchTitles: {
      ...(clear // The black magic which empties all titles
        ? Object.values(URI_KEY_FOR).reduce(
            (obj, key) => ({ ...obj, [key]: null }),
            {}
          )
        : null),
      [URI_KEY_FOR.PATH]: pages[slide].title, // Except the only one of a current page
    },
  });
};

