import React from "react";
import AnimatedArrow from "../AnimatedArrow";
import styles from "./styles.module.scss";
import { getCurrentLocationReplacing } from "../utils";
import { getData } from "../DataJson";

const PageSwitch = ({ text, onSwitch, slideNumber = 0, slides = 0 }) => {
  const { pages } = getData();
  const prevSlideNumber = (slideNumber - 1 + slides) % slides;
  const nextSlideNumber = (slideNumber + 1) % slides;
  return (
    <div className={styles.container}>
      <AnimatedArrow
        direction="left"
        link={
          getCurrentLocationReplacing({
            search: null,
            pathname: pages[prevSlideNumber].path,
          }).url
        }
        onClick={() => onSwitch && onSwitch(-1)}
      />
      <div className={styles.centerContainer}>
        <div className={styles.text}>{text || "?"}</div>
        <div className={styles.dotsContainer}>
          {Array.from({ length: slides }).map((_, slide) => (
            <div
              key={slide}
              className={slide === slideNumber ? styles.selectedDot : ""}
              onClick={() =>
                onSwitch && onSwitch(Math.sign(slide - slideNumber))
              }
            />
          ))}
        </div>
      </div>
      <AnimatedArrow
        direction="right"
        link={
          getCurrentLocationReplacing({
            search: null,
            pathname: pages[nextSlideNumber].path,
          }).url
        }
        onClick={() => onSwitch && onSwitch(1)}
      />
    </div>
  );
};

export default PageSwitch;
