import React from "react";
import Icon from "../Icon";
import styles from "./styles.module.scss";

const OffersVisual = ({ data = [] } = {}) => {
  return [
    <div key="offers-heading" className={styles.title}>
      What Can I Offer
    </div>,
    <div className={styles.container} key="offers-body">
      {data.map(({ icon, caption, description }, i) => {
        return (
          <div key={i} className={styles.item}>
            <Icon
              image={icon}
              size="m"
              className={
                styles.icon +
                (styles[`icon-${icon}`] ? " " + styles[`icon-${icon}`] : "")
              }
            />
            <div>
              <div className={styles.caption}>{caption}</div>
              <p className={styles.description}>{description}</p>
            </div>
          </div>
        );
      })}
    </div>,
  ];
};

export default OffersVisual;
