import React from "react";
import styles from "./styles.module.scss";
import * as icons from "./icons";

const Icon = ({
  image,
  size = "m",
  hoverable = false,
  rounded = false,
  margin = "s",
  className,
  shadow = false,
  style,
  imageonly = false,
  onClick,
}) => {
  const Icon = icons[image] || icons.twitter;
  const imageStyle = styles[`image-${image}`];
  const sizeStyle = styles[`size-${size.toLowerCase()}`];
  return (
    <div
      style={style || null}
      className={`${styles.icon}${hoverable ? ` ${styles.hoverable}` : ""} ${
        styles[`margin-${margin && !imageonly ? margin : "none"}`]
      }${rounded ? ` ${styles.rounded}` : ""}${
        imageStyle ? ` ${imageStyle}` : ""
      }${sizeStyle && !imageonly ? ` ${sizeStyle}` : ""}${
        className ? ` ${className}` : ""
      }${shadow ? ` ${styles.shadow}` : ""}`}
      onClick={(e) => onClick && onClick(e)}
    >
      <Icon />
    </div>
  );
};

export default Icon;
