import React, { useEffect, useState } from "react";

let latestData = null;

export const getData = () => latestData;

export const DataJsonWrapper = ({ Component }) => {
  const [data, setData] = useState(null);

  useEffect(() => {
    (async () => {
      const newData = await fetch(`/nikitaeverywhere-com-content/data.json`);
      latestData = await newData.json();
      setData(latestData);
    })();
  }, []);

  return data ? (
    <Component />
  ) : (
    <div className="loaderWrapper">
      <div className="loader"></div>
    </div>
  );
};
