import React, { useCallback, useState, useEffect, useRef } from "react";
import { reportEvent } from "../../../utils";
import styles from "./styles.module.scss";

const MIN_SCALE = 1;
const MAX_SCALE = 10;

const reportZoomUsage = (imageData = {}) =>
  reportEvent({
    category: "imageViewer",
    action: "zoomUsed",
    label: imageData.w > imageData.h * 2.5 ? "panorama" : "picture",
    sendOnce: true,
  });

const GalleryView = ({ data, zoomChanges } = {}) => {
  // const [zooming, setZooming] = useState(false);
  // const [ox, setOx] = useState(0);
  // const [oy, setOy] = useState(0);
  // const [sx, setSx] = useState(0);
  // const [sy, setSy] = useState(0);
  // const [od, setOd] = useState(1);
  // const [cx, setCx] = useState(0);
  // const [cy, setCy] = useState(0);
  // const [cd, setCd] = useState(1);
  // const [sd, setSd] = useState(1);

  const [state, setState] = useState({
    ox: 0,
    oy: 0,
    sx: 0,
    sy: 0,
    od: 1,
    cx: 0,
    cy: 0,
    cd: 1,
    zooming: false,
  });
  const container = useRef(null);
  const image = useRef(null);

  // const [ld, setLd] = useState(1); // last distance
  const touchStart = useCallback(
    (e) => {
      if (
        !state.zooming &&
        e.touches &&
        e.touches.length === 2 &&
        data.type === "image"
      ) {
        e.stopPropagation();
        let x = (e.touches[0].screenX + e.touches[1].screenX) / 2;
        let y = (e.touches[0].screenY + e.touches[1].screenY) / 2;
        const ssd = Math.sqrt(
          Math.pow(e.touches[0].screenX - e.touches[1].screenX, 2) +
            Math.pow(e.touches[0].screenY - e.touches[1].screenY, 2)
        );
        // console.log(`S (${x},${y}) <<<<<<<<<<<<<<<<<<<<<<<<<<<`);
        // setCx(x);
        // setCy(y);
        // setSx(x);
        // setSy(y);
        // setOx(x - (cx - ox));
        // setOy(y - (cy - oy));
        // setOd(ssd * (od / cd));
        // setSd(ssd);
        // setCd(Math.sqrt(
        //   Math.pow(e.touches[0].screenX - e.touches[1].screenX, 2)
        //   + Math.pow(e.touches[0].screenY - e.touches[1].screenY, 2)
        // ));
        // setZooming(true);
        // console.log('!!!!', od / cd);
        reportZoomUsage(data);
        setState({
          ...state,
          cx: x,
          cy: y,
          sx: x,
          sy: y,
          ox: x - (state.cx - state.ox),
          oy: y - (state.cy - state.oy),
          od:
            ssd *
            Math.max(
              Math.min(1 / MIN_SCALE, state.od / state.cd),
              1 / MAX_SCALE
            ),
          sd: ssd,
          cd: Math.sqrt(
            Math.pow(e.touches[0].screenX - e.touches[1].screenX, 2) +
              Math.pow(e.touches[0].screenY - e.touches[1].screenY, 2)
          ),
          zooming: true,
        });
      }
    },
    [state, data]
  ); //[zooming, data.type, od, cd, cx, cy, ox, oy]);
  const touchMove = useCallback(
    (e) => {
      if (e.touches.length < 2) {
        setState({ ...state, zooming: false });
        // setZooming(false);
        return;
      }
      if (!state.zooming) {
        return;
      }
      e.stopPropagation();
      const x = (e.touches[0].screenX + e.touches[1].screenX) / 2;
      const y = (e.touches[0].screenY + e.touches[1].screenY) / 2;
      // console.log(`M (${x},${y}); ${x - state.sx} ${y - state.sy}`);
      const cd = Math.sqrt(
        Math.pow(e.touches[0].screenX - e.touches[1].screenX, 2) +
          Math.pow(e.touches[0].screenY - e.touches[1].screenY, 2)
      );
      // setCx(x);
      // setCy(y);
      // setCd(cd);
      setState({
        ...state,
        cx: x,
        cy: y,
        cd: Math.min(Math.max(cd, state.od * MIN_SCALE), state.od * MAX_SCALE),
      });
    },
    [state]
  );
  const touchEnd = useCallback(() => {
    // console.log(`E (?,?) <<<<<<<<<<<<<<<<<< zooming=${state.zooming}`);
    if (state.zooming) {
      // setZooming(false);
      setState({ ...state, zooming: false });
    }
  }, [state]);

  useEffect(() => {
    if (!zoomChanges) {
      return;
    }
    const updater = (value) => {
      // setCd(value > 0
      //   ? cd * 2
      //   : cd / 2);
      reportZoomUsage(data);
      setState({
        ...state,
        cd:
          value > 0
            ? Math.min(MAX_SCALE * state.od, state.cd * 2)
            : Math.max(MIN_SCALE * state.od, state.cd / 2),
      });
    };
    zoomChanges.on(updater);
    return () => zoomChanges.off(updater);
  }, [data, state, zoomChanges]);

  const { cx, cy, od, cd, ox, oy } = state;

  let scale = od && cd ? cd / od : 1;
  // console.log(cx - sx, cy - sy);

  // This makes touch zooming and panning correct at any scale
  // but for some reason it screws up starting coordinates...
  let shiftX = cx - ox - (cx - ox) * (1 - scale); // + (cx - sx) * (1 - scale);
  let shiftY = cy - oy - (cy - oy) * (1 - scale); // + (cy - sy) * (1 - scale);

  shiftX =
    container && container.current && image && image.current
      ? Math.min(
          Math.max(
            (container.current.clientWidth / 2 -
              (container.current.clientWidth - image.current.clientWidth) / 2) *
              (1 - scale),
            shiftX
          ),
          -(
            container.current.clientWidth / 2 -
            (container.current.clientWidth - image.current.clientWidth) / 2
          ) *
            (1 - scale)
        )
      : shiftX;
  shiftY =
    container && container.current && image && image.current
      ? Math.min(
          Math.max(
            (container.current.clientHeight / 2 -
              (container.current.clientHeight - image.current.clientHeight) /
                2) *
              (1 - scale),
            shiftY
          ),
          -(
            container.current.clientHeight / 2 -
            (container.current.clientHeight - image.current.clientHeight) / 2
          ) *
            (1 - scale)
        )
      : shiftY;

  return (
    <div
      className={styles.contentContainer}
      onTouchStart={(e) => touchStart(e)}
      onTouchMove={(e) => touchMove(e)}
      onTouchEnd={(e) => touchEnd(e)}
      ref={container}
    >
      {data.type === "image" ? (
        <img
          src={data.src || data.thumbnail}
          alt={data.caption}
          ref={image}
          style={{
            transform: `translate(${shiftX}px, ${shiftY}px) scale(${scale})`,
          }}
        />
      ) : data.type === "youtube" ? (
        <div className={styles.embeddedVideo}>
          <iframe
            title={data.caption}
            src={data.src}
            frameBorder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          />
        </div>
      ) : (
        <div>
          <div>
            This media is not supported by image viewer. Contact Nikita, it's a
            bug! ;)
          </div>
          <div className={styles.sub}>
            type={data.type || "<undefined>"} | src={data.src}
          </div>
        </div>
      )}
    </div>
  );
};

export default GalleryView;
