import React from "react";
import Icon from "../Icon";
import CircularProgressbar from "../CircularProgressbar";
import styles from "./styles.module.scss";

const IconWithProgress = ({
  progress = 0.5,
  link,
  subElement = null,
  ...iconProps
}) => {
  const Tag = link ? "a" : "div";
  iconProps.margin = "none";
  iconProps.hoverable = link ? true : iconProps.hoverable || false;
  iconProps.className = styles[`icon-${iconProps.image}`] || "";
  return (
    <Tag
      className={styles.socialIcon}
      {...(!link ? null : { href: link, target: "_blank" })}
    >
      <CircularProgressbar progress={progress}>
        <Icon {...iconProps} />
      </CircularProgressbar>
      {subElement && <div className={styles.iconFooter}>{subElement}</div>}
    </Tag>
  );
};

export default IconWithProgress;
