import React from "react";
import styles from "./styles.module.scss";

const AnimatedArrow = ({ direction, onClick, link } = {}) => {
  const Element = link ? "a" : "div";
  const onElementClick = (event) => {
    if (link) {
      event.preventDefault();
    }
    if (onClick) {
      onClick(event);
    }
  };
  return (
    <Element
      className={`${styles.round} ${styles[`dir-${direction}`]}`}
      onClick={onElementClick}
      href={link ? link : null}
    >
      <div className={styles.cta}>
        <span
          className={`${styles.arrow} ${styles.arr1} ${styles.next}`}
        ></span>
        <span
          className={`${styles.arrow} ${styles.arr2} ${styles.next}`}
        ></span>
      </div>
    </Element>
  );
};

export default AnimatedArrow;
