import React from "react";
import Icon from "../Icon";
import styles from "./styles.module.scss";

const CareerPathVisual = ({ contents = [] }) => {
  return [
    <div key="1" className={styles.title}>
      Full-Time Career
    </div>,
    <div key="2" className={styles.wrapper}>
      <div className={styles.table}>
        {contents.map(({ icon, caption, year, subtitle }, i) => (
          <div className={styles.row} key={i}>
            <div className={styles.column}>{year || ""}</div>
            <div className={styles.column}>
              <Icon image={icon} className={styles.icon} size="m" />
            </div>
            <div
              className={`${styles.column}${
                subtitle ? ` ${styles.detailedColumn}` : ""
              }`}
            >
              <div>{caption}</div>
              <div className={styles.sub}>{subtitle || ""}</div>
            </div>
          </div>
        ))}
      </div>
    </div>,
  ];
};

export default CareerPathVisual;
