import React from "react";
import {
  getCurrentLocationReplacing,
  setCurrentUrlReplacing,
  getUrlSearchKey,
  URI_KEY_FOR,
} from "../utils";
import Icon from "../Icon";
import styles from "./styles.module.scss";

const urlValuesDelimiter = "~";
const tagDefaultOrder = (a, b) => a.name > b.name;

// Trigger onTagsChange only once, globally, on the page open.
let globalFirstOpen = true;

const TagLine = ({
  tags = [],
  onTagsChange,
  jumpToFront = false,
  className,
} = {}) => {
  if (globalFirstOpen) {
    const urlData = getUrlSearchKey(URI_KEY_FOR.TAGLINE);
    const urlSelectedSet = new Set(
      urlData ? urlData.split(urlValuesDelimiter) : []
    );
    const matchesUrl = tags.reduce(
      (acc, tag) => acc && tag.isSelected === urlSelectedSet.has(tag.id),
      true
    );
    globalFirstOpen = false;
    if (!matchesUrl && onTagsChange) {
      onTagsChange(
        tags.map((tag) => {
          return tag.isSelected === urlSelectedSet.has(tag.id)
            ? tag
            : { ...tag, isSelected: urlSelectedSet.has(tag.id) };
        })
      );
      return null;
    }
  }

  const orderedTags = tags.sort(tagDefaultOrder);

  const toRender = jumpToFront
    ? orderedTags
        .filter(({ isSelected }) => isSelected)
        .concat(orderedTags.filter(({ isSelected }) => !isSelected))
    : orderedTags;

  const onTagSelect = (clickEvent, clickedTag) => {
    clickEvent.preventDefault();
    if (!onTagsChange) {
      return;
    }
    const newTags = tags.map((tag) => {
      return tag === clickedTag ? { ...tag, isSelected: !tag.isSelected } : tag;
    });
    setCurrentUrlReplacing({
      search: {
        [URI_KEY_FOR.TAGLINE]:
          newTags
            .slice()
            .sort(tagDefaultOrder)
            .reduce((acc, tag) => {
              if (tag.isSelected) {
                acc.push(tag.id);
              }
              return acc;
            }, [])
            .join(urlValuesDelimiter) || null,
      },
    });
    onTagsChange(newTags);
  };

  return (
    <div className={`${styles.container}${className ? ` ${className}` : ""}`}>
      {toRender.map((tag, i) => {
        const Element = onTagsChange ? "a" : "span";
        return (
          <Element
            key={tag.id || i}
            className={`${styles.tag}${
              onTagsChange ? ` ${styles.selectable}` : ""
            }${tag.isSelected ? ` ${styles.selectedTag}` : ""}`}
            onClick={(e) => onTagSelect(e, tag)}
            href={
              Element !== "a"
                ? null
                : getCurrentLocationReplacing({
                    search: {
                      [URI_KEY_FOR.TAGLINE]: tag.id,
                      gallery: null,
                    },
                  }).url
            }
          >
            {onTagsChange && (
              <Icon
                image={tag.isSelected ? "tick" : "cross"}
                size="s"
                className={styles.tickIcon}
              />
            )}
            {tag.name}
          </Element>
        );
      })}
    </div>
  );
};

export default TagLine;
