import React from "react";
import styles from "./styles.module.scss";

const Responsive2ColumnLayout = ({ children = [] }) => {
  if (children.length < 2) {
    return <div>{children}</div>;
  }
  return (
    <div className={children[1] && styles.container}>
      {children[0]}
      {children[1]}
    </div>
  );
};

export default Responsive2ColumnLayout;
