const isLocal = window.location.hostname === 'localhost' || /0-9/.test(window.location.hostname[0]);
const reportingDisabledLocal = window.localStorage.getItem('noreporting');
let [, reportingDisabledUrlParam, reportingPermanentFlag] = window.location.search.match(/(?:&|\?)(noreporting)=?([^&]+)?/) || [undefined, reportingDisabledLocal === 'permanent' ? 'noreporting' : undefined, reportingDisabledLocal];

// &noreporting is added by proxy when crawled by search engine / bot, thus making analytics human-only.
// For devices I frequently use for testing, &noreporting=permanent will disable reporting from that device forever (as far as localstorage persists).
if (reportingPermanentFlag) {
  try {
    window.localStorage.setItem(reportingDisabledUrlParam, reportingPermanentFlag);
  } catch (e) { }
}

// Remove noreporting from URL (because links on the website will start to have it in case of prerendering)
if (/(?:\?|&)noreporting/.test(window.location.search)) {
  window.history.pushState(
    {},
    document.title,
    window.location.href.replace(/(?:\?|&)noreporting(?:=[^&]*)?/g, '')
  );
}

export const reportPageview = () => {
  if (reportingDisabledUrlParam || isLocal || typeof window.ga !== 'function') {
    return;
  }
  const path = window.location.pathname + window.location.search + window.location.hash;
  window.ga('send', 'pageview', path);
};

let alreadyReportedEvents = new Set();
export const reportEvent = ({
  category = 'undefinedEventCategory',
  action = 'undefinedEventAction',
  label, // Optional
  value, // Numeric only!
  sendOnce = false, // Send this type of event only once per session
} = {}) => {
  if (isLocal || typeof window.ga !== 'function') {
    return;
  }
  if (sendOnce) {
    const sum = [category, action, label, value].join('|');
    if (alreadyReportedEvents.has(sum)) {
      return;
    }
    alreadyReportedEvents.add(sum);
  }
  window.ga('send', 'event', category, action, label, value);
};