import React from "react";
import IconWithProgress from "../IconWithProgress";
import Responsive2ColumnLayout from "../Responsive2ColumnLayout";
import styles from "./styles.module.scss";

const TechByCategoryTable = ({ data = [] }) => {
  return (
    <div>
      <div className={styles.mainTitle}>My Experience</div>
      <div className={styles.subtitle}>This list is for overview purposes.</div>
      {data.map(({ categoryName = "Unnamed", items = [] }, i) => {
        return (
          <Responsive2ColumnLayout key={i}>
            <div>
              <div className={styles.heading}>{categoryName}</div>
            </div>
            <div className={styles.iconsContainer}>
              {items.map(({ icon, link, name, progress = 0.5 }, i) => (
                <IconWithProgress
                  key={i}
                  image={icon}
                  link={link}
                  subElement={name}
                  progress={progress}
                />
              ))}
            </div>
          </Responsive2ColumnLayout>
        );
      })}
    </div>
  );
};

export default TechByCategoryTable;
