import queryString from "querystring";
import { reportPageview } from "./analytics";
import { getData } from "../DataJson";

/**
 * Keys that identify which element should be activated depending on URL changes.
 */
export const URI_KEY_FOR = {
  TIMELINE: "show",
  TAGLINE: "select",
  GALLERY: "gallery",
  PATH: "path",
};
const tryF = (f) => {
  try {
    return f();
  } catch (e) {
    console.error(e);
    return undefined;
  }
};

/**
 * There's a global state of which title to display depending on which titles
 * are currently in the state.
 */
let titleStateByUriKeys = {};

const updateTitleFromState = () => {
  const { websitePersistentTitle, website } = getData();
  const timelineTitle = titleStateByUriKeys[URI_KEY_FOR.TIMELINE]
    ? titleStateByUriKeys[URI_KEY_FOR.TIMELINE]
    : "";
  const galleryTitle = titleStateByUriKeys[URI_KEY_FOR.GALLERY]
    ? titleStateByUriKeys[URI_KEY_FOR.GALLERY]
    : "";
  const pathTitle = titleStateByUriKeys[URI_KEY_FOR.PATH]
    ? titleStateByUriKeys[URI_KEY_FOR.PATH]
    : "";
  const preTitle = `${galleryTitle}${
    timelineTitle && galleryTitle ? " - " : ""
  }${timelineTitle}`;

  const title = `${preTitle}${preTitle ? " | " : ""}${websitePersistentTitle}${
    pathTitle ? " " : ""
  }${pathTitle}`;
  const desc = `${
    galleryTitle
      ? `Check the image "${galleryTitle}". `
      : timelineTitle
      ? `Check the article "${timelineTitle}". `
      : ""
  }${websitePersistentTitle}${pathTitle ? " " : ""}${
    pathTitle || " - General Profile"
  }. Learn more on ${websitePersistentTitle}'s Website.`;

  window.document.title = title;
  tryF(() =>
    document
      .querySelector("meta[name=description]")
      .setAttribute("content", desc)
  );
  tryF(() =>
    document
      .querySelector('meta[property="og:title"]')
      .setAttribute("content", title)
  );
  tryF(() =>
    document.querySelector("meta[name=title]").setAttribute("content", title)
  );
  tryF(() =>
    document
      .querySelector('meta[property="og:url"]')
      .setAttribute(
        "content",
        `${website}${window.location.pathname}${window.location.search}`
      )
  );
  tryF(() =>
    requestAnimationFrame(
      () =>
        document.getElementById("avatar") &&
        document
          .querySelector('meta[property="og:image"]')
          .setAttribute(
            "content",
            (
              (document.querySelector("img") &&
                document.querySelector("img").src) ||
              window
                .getComputedStyle(document.getElementById("avatar"), false)
                ["background-image"].match(/url\(["']?([^"']*)["']?\)/)[1]
            ).replace(/^https?:\/\/[^/]+/, website)
          )
    )
  );
  return title;
};

const updateCannonicalLinkElement = (canonicalHref) => {
  const element = document.querySelector("link[rel=canonical]");
  if (element && canonicalHref) {
    element.setAttribute("href", canonicalHref);
  } else if (element && !canonicalHref && element.parentNode) {
    element.parentNode.removeChild(element);
  } else if (!element && canonicalHref) {
    const el = document.createElement("link");
    el.setAttribute("rel", "canonical");
    el.setAttribute("href", canonicalHref);
    document.head.appendChild(el);
  }
};

// Rule: match post tags with pages tags from left to right. The match closest match found determines the canonical page.
const getCanonicalPageIndex = (articleTags, allPages) => {
  for (const tag of articleTags) {
    let minPos = Infinity;
    let minPosPageIndex = -1;
    for (let i = 0; i < allPages.length; ++i) {
      const { timelineFilter } = allPages[i];
      if (!timelineFilter || !timelineFilter.tags) {
        continue;
      }
      const indexInTags = timelineFilter.tags.indexOf(tag);
      if (indexInTags === -1) {
        continue;
      }
      if (indexInTags < minPos) {
        minPos = indexInTags;
        minPosPageIndex = i;
      }
    }
    if (minPosPageIndex !== -1) {
      // We exit imediately because we're interested in finding the 1st tag
      return minPosPageIndex;
    }
  }
  return -1;
};

const resolveCanonicalPageForArticle = ({ pathname }, articleTags) => {
  const { pages } = getData();
  if (!articleTags || articleTags.length === 0) {
    return "";
  }
  const allPages = Array.from({ ...pages, length: Object.keys(pages).length });
  let currentPageIndex =
    allPages.findIndex(
      ({ path }) => path !== "/" && (pathname || "").indexOf(path) === 0
    ) || 0;
  currentPageIndex = currentPageIndex === -1 ? 0 : currentPageIndex;
  const canonicalPageIndex = getCanonicalPageIndex(articleTags, allPages);
  return canonicalPageIndex === -1 || currentPageIndex === canonicalPageIndex
    ? ""
    : getCurrentLocationReplacing({
        pathname: allPages[canonicalPageIndex].path,
      }).url;
};

// When the same article is available on another page, we should link the canonical (main) article on that page
// from all non-canonical (non-main) pages.
const updateCannonicalLink = (location, articleTags) => {
  const canonicalHref = resolveCanonicalPageForArticle(location, articleTags);
  updateCannonicalLinkElement(canonicalHref);
};

export const getCurrentLocationReplacing = ({
  search = {}, // Passing null as a key value here will clear this search param, as well as passing null as value itself
  pathname = window.location.pathname,
  origin = "", // By default return relative paths
} = {}) => {
  let qs;
  if (search !== null) {
    // Else - clears
    const decodedQs = queryString.decode(window.location.search.slice(1));
    const sortedKeys = Array.from(
      new Set(Object.keys(decodedQs).concat(Object.keys(search)))
    ).sort();
    search = sortedKeys.reduce((acc, key) => {
      if (search[key] === null) {
        delete acc[key];
      } else if (typeof search[key] !== "undefined") {
        acc[key] = search[key];
      } else {
        acc[key] = decodedQs[key];
      }
      return acc;
    }, {});
    qs = queryString.encode(search);
  }
  pathname =
    pathname +
    (pathname && pathname[pathname.length - 1] !== "/" && qs ? "/" : "");
  origin =
    origin && typeof origin !== "string"
      ? window.location.origin // If origing is not a string return default origin
      : origin;
  return {
    url: `${origin}${pathname}${qs ? `?${qs}` : ""}`,
    search: Object.assign({}, search),
    pathname,
    origin,
  };
};

/**
 * A very important function which updates URLs and titles according to new given values.
 * It is component's responsibility to keep the state clean.
 */
export const setCurrentUrlReplacing = ({
  searchTitles = {}, // key-value of search key -> title string
  ...urlParams
} = {}) => {
  // Update titles state
  if (searchTitles) {
    for (const [searchKey, title] of Object.entries(searchTitles)) {
      if (title) {
        titleStateByUriKeys[searchKey] = title;
      } else {
        delete titleStateByUriKeys[searchKey];
      }
    }
  } else {
    titleStateByUriKeys = {};
  }
  const title = updateTitleFromState();
  const location = getCurrentLocationReplacing(urlParams);
  window.history.pushState({}, title, location.url);
  updateCannonicalLink(
    location,
    urlParams.metadata && urlParams.metadata.postTags
  );
  reportPageview();
};

export const getUrlSearchKey = (keyName) => {
  const uriData = queryString.decode(window.location.search.slice(1));
  return uriData[keyName];
};
