import React, { useState, useCallback, Fragment } from "react";
import Carousel, { Modal, ModalGateway } from "react-images";
import {
  getCurrentLocationReplacing,
  setCurrentUrlReplacing,
  getStringSlug,
  getUrlSearchKey,
  URI_KEY_FOR,
} from "../utils";
import Footer from "./components/Footer";
import CustomView from "./components/View";
import CustomHeader from "./components/Header";
import Icon from "../Icon";
import JustifiedGrid from "react-justified-grid";
import styles from "./styles.module.scss";

class ZoomEventEmitter {
  _listeners = [];
  on(f) {
    this._listeners.push(f);
  }
  off(f) {
    this._listeners = this._listeners.filter((l) => l !== f);
  }
  emit(v) {
    for (const listener of this._listeners) {
      listener(v);
    }
  }
}

let lightboxGlobalFirstRender = true;

const Gallery = ({ images = [], postTags = [] }) => {
  const uriImageSrc = getUrlSearchKey(URI_KEY_FOR.GALLERY); // TODO: optimize, parse only on first open
  const initiallySelectedIndex = uriImageSrc
    ? images.findIndex(({ src }) => uriImageSrc === getStringSlug(src))
    : -1;
  const [state, setState] = useState({
    selectedIndex: initiallySelectedIndex,
    lightboxIsOpen: initiallySelectedIndex !== -1,
    zoomingListeners: new ZoomEventEmitter(),
  });
  const updateUrlToImageNumber = useCallback(
    (index, isOpen = true) => {
      setCurrentUrlReplacing({
        search: {
          [URI_KEY_FOR.GALLERY]: isOpen
            ? getStringSlug(images[index] && images[index].src)
            : null,
        },
        searchTitles: {
          [URI_KEY_FOR.GALLERY]:
            index >= 0
              ? images[index] && images[index].caption
                ? images[index].caption
                : "Image"
              : null,
        },
        metadata: {
          postTags,
        },
      });
    },
    [images, postTags]
  );
  const toggleLightbox = useCallback(
    (clickEvent, index) => {
      clickEvent.preventDefault();
      const isOpen = !state.lightboxIsOpen;
      updateUrlToImageNumber(index, !state.lightboxIsOpen);
      setState({
        ...state,
        lightboxIsOpen: isOpen,
        selectedIndex: index,
      });
    },
    [state, updateUrlToImageNumber]
  );

  if (lightboxGlobalFirstRender && state.lightboxIsOpen) {
    lightboxGlobalFirstRender = false;
    updateUrlToImageNumber(initiallySelectedIndex, state.lightboxIsOpen);
  }

  const Header = (props) => (
    <CustomHeader
      {...props}
      zoom={(zoom) => state.zoomingListeners.emit(zoom)}
    />
  );
  const View = (props) => (
    <CustomView {...props} zoomChanges={state.zoomingListeners} />
  );

  return (
    <div>
      <JustifiedGrid
        images={images.map((o) => ({
          ...o,
          width: o.w || 160,
          height: o.h || 90,
        }))}
        rows={100500}
        maxRowHeight={150}
        showIncompleteRow
        gutter={2}
      >
        {(images) => (
          <Fragment>
            {images.map((image, i) => {
              const { width, height, left, top, originalData, rowOffset } =
                image;
              const isLastRow =
                images[images.length - 1].rowOffset === rowOffset;
              let lastRowWidth = 0;
              for (let i = images.length - 1; i >= 0; --i) {
                if (images[i].rowOffset !== rowOffset) {
                  break;
                }
                lastRowWidth += images[i].width;
              }
              const realLeft = isLastRow
                ? `calc(50% - ${lastRowWidth / 2}px + ${left}px)`
                : left;
              return (
                <a
                  key={originalData.src || i}
                  className={styles.link}
                  style={{ left: realLeft, top }}
                  href={
                    getCurrentLocationReplacing({
                      search: {
                        [URI_KEY_FOR.GALLERY]: getStringSlug(originalData.src),
                      },
                    }).url
                  }
                  onClick={(e) => toggleLightbox(e, i)}
                >
                  <img
                    src={originalData.thumbnail || originalData.src}
                    width={width}
                    height={height}
                    alt={originalData.caption || "Image"}
                  />
                  {originalData.type === "youtube" && (
                    <Icon
                      className={styles.thumbnailCenterIcon}
                      image="youtube"
                      shadow
                      size="m"
                    />
                  )}
                </a>
              );
            })}
          </Fragment>
        )}
      </JustifiedGrid>

      <ModalGateway>
        {state.lightboxIsOpen ? (
          <Modal
            onClose={toggleLightbox}
            allowFullscreen={true}
            closeOnBackdropClick={true}
          >
            <Carousel
              components={{ Footer, View, Header }}
              currentIndex={state.selectedIndex}
              frameProps={{ autoSize: "height" }}
              views={images}
              trackProps={{
                onViewChange: (imageIndex) =>
                  updateUrlToImageNumber(imageIndex),
              }}
            />
          </Modal>
        ) : null}
      </ModalGateway>
    </div>
  );
};

export default Gallery;
