import React, { useCallback } from "react";
import Icon from "../Icon";
import styles from "./styles.module.scss";

const mapCss = ({ code, id }) =>
  `.${styles.mapContainer} path#${(id || code).toUpperCase()}`;

const WorldMap = ({ areas = [], onSelect }) => {
  const selectedAreas = areas.filter(({ isSelected }) => isSelected);
  const inlineStyle =
    areas.length &&
    `${areas.map(mapCss).join(",")} {
    fill: ${styles["--fillColor"]};
    stroke: ${styles["--strokeColor"]};
    stroke-width: ${styles["--strokeWidth"]};
  } ${selectedAreas.length && selectedAreas.map(mapCss).join(",")}${
      selectedAreas.length &&
      `{
    fill: ${styles["--selectedColor"]};
  }`
    }`;

  const mapClick = useCallback(
    (clickEvent) => {
      if (
        typeof onSelect !== "function" ||
        !clickEvent.target ||
        !clickEvent.target.hasAttribute("id")
      ) {
        return;
      }
      const id = clickEvent.target.getAttribute("id");
      const area = areas.find((area) => area.id === id);
      if (area) {
        onSelect(area);
      }
    },
    [areas, onSelect]
  );

  return (
    <div className={styles.mapContainer}>
      <style>{inlineStyle}</style>
      <Icon
        imageonly
        image="worldmap"
        className={styles.mapIcon}
        onClick={mapClick}
      />
    </div>
  );
};

export default WorldMap;
