import React from "react";
import Icon from "../../../Icon";
import styles from "./styles.module.scss";

const GalleryHeader = (props = {}) => {
  const {
    getStyles = () => ({}),
    isModal = true,
    modalProps = {},
    zoom = () => {},
  } = props;
  if (!isModal) return null;
  const { allowFullscreen, isFullscreen, onClose, toggleFullscreen } =
    modalProps;

  return (
    <div style={getStyles("header", props)}>
      <span></span>
      <span>
        <Icon
          image="zoomIn"
          hoverable
          className={styles.topIcon}
          onClick={() => zoom(1)}
        />
        <Icon
          image="zoomOut"
          hoverable
          className={styles.topIcon}
          onClick={() => zoom(-1)}
        />
        {allowFullscreen && (
          <Icon
            image={`fullscreen${isFullscreen ? "Exit" : "Enter"}`}
            hoverable
            className={styles.topIcon}
            onClick={toggleFullscreen}
          />
        )}
        <Icon
          image="cross"
          hoverable
          className={styles.topIcon}
          onClick={onClose}
        />
      </span>
    </div>
  );
};

export default GalleryHeader;
