import React, { useState } from "react";
import Icon from "../Icon";
import { reportEvent } from "../utils";
import styles from "./styles.module.scss";

const linkReport = (to) => {
  reportEvent({
    category: "socialLinkIcon",
    action: "clicked",
    label: to,
  });
};

const SocialIcon = ({
  image,
  size = "m",
  link,
  featureIcon = "users",
  featureValue = -42,
  expandedContent = "???",
  expandedContentSubtext = null,
  expanded = false,
  expandable = false,
  onExpand = null,
  onClick = null,
  rounded = true,
}) => {
  const [isExpanded, setExpanded] = useState(expanded);
  const Tag = link ? "a" : "div";
  return (
    <Tag
      className={styles.socialIcon}
      {...(!link
        ? null
        : {
            href: link,
            target: "_blank",
            onClick: () => linkReport(link),
          })}
    >
      <div
        className={`${styles.content}${
          styles[`icon-${image}`] ? ` ${styles[`icon-${image}`]}` : ""
        }`}
      >
        <Icon
          className={styles.icon}
          image={image}
          size={size}
          margin="none"
          hoverable
          rounded={rounded}
          onClick={
            expandable
              ? () => {
                  if (onExpand) {
                    onExpand();
                  }
                  setExpanded(!isExpanded);
                }
              : onClick
          }
        />
        {isExpanded && expandedContent && (
          <div className={styles.expandedContentContainer}>
            <div>{expandedContent}</div>
            {expandedContentSubtext && (
              <div className={styles.expandedContentSubtext}>
                {expandedContentSubtext}
              </div>
            )}
          </div>
        )}
      </div>
      {featureValue || featureIcon ? (
        <div
          className={styles.iconFooter}
          style={{ width: `var(--size-${size})` }}
        >
          {featureIcon && <Icon image={featureIcon} size="xs" margin="xs" />}
          <span>{featureValue}</span>
        </div>
      ) : null}
    </Tag>
  );
};

export default SocialIcon;
