import React from "react";
import styles from "./styles.module.scss";

const radius = 175;
const diameter = Math.round(Math.PI * radius * 2);
const getOffset = (val = 0) =>
  Math.round(((100 - Math.min(val * 100, 100)) / 100) * diameter);

const CircularProgressBar = ({
  progress = 0.5,
  children = null,
  className,
  style = null,
}) => {
  const strokeDashoffset = getOffset(progress);
  const strokeLinecap = "round";
  return (
    <div
      className={`${styles.container}${className ? ` ${className}` : ""}`}
      style={style}
    >
      <svg className={styles.svgElement} viewBox="-25 -25 400 400">
        <circle
          stroke="currentColor"
          transform="rotate(-90 175 175)"
          cx="175"
          cy="175"
          r="175"
          strokeDasharray="1100"
          strokeWidth="5%"
          strokeDashoffset="1100"
          strokeLinecap={strokeLinecap}
          fill="none"
          style={{
            strokeDashoffset,
            transition: `stroke-dashoffset 0.3s ease-out`,
          }}
        />
      </svg>
      <div className={styles.subContainer}>{children}</div>
    </div>
  );
};

export default CircularProgressBar;
