import React from "react";
import styles from "./styles.module.scss";

const Header = ({ transition, slideNumber, nextSlideNumber }) => {
  return (
    <div className={styles.head}>
      {transition === 0 ? null : (
        <div
          className={`${styles.cover} headParallax ${
            styles[`slide-${nextSlideNumber}-background`]
          }`}
        />
      )}
      <div
        className={`${styles.cover} headParallax ${
          styles[`slide-${slideNumber}-background`]
        }`}
        style={{ opacity: Math.max(0, Math.min(1, 1 - Math.abs(transition))) }}
      />
    </div>
  );
};

export default Header;
