import React, { useCallback, useState } from "react";
import Gallery from "../Gallery";
import WorldMap from "../WorldMap";
import TagLine from "../TagLine";
import styles from "./styles.module.scss";
import { getData } from "../DataJson";

// TODO: component state does not react to prop changes. Use isSelected as the _only_ state prop here (do not map state)
const WorldPhotoMap = ({ data = [] }) => {
  const { galleryMaxFeatured } = getData();
  const photoTimeline = data.filter(
    ({ tags }) => tags && tags.includes("photography")
  );
  // uniqueView is used to re-render Gallery only once, as TagLine may change URL and Gallery needs to update
  const [uniqueView, setUniqueView] = useState(0);
  const [areas, setAreas] = useState(
    photoTimeline
      .map(({ location, media }, i) => {
        if (!media || !media.length || !location || !location.code) {
          return null;
        }
        return {
          name: location.name || location.code,
          id: location.code,
          isSelected: false,
        };
      })
      .filter((d) => !!d)
  );
  const updateSelected = useCallback(
    ({ id }) => {
      setAreas(
        areas.map((area) =>
          area.id === id ? { ...area, isSelected: !area.isSelected } : area
        )
      );
    },
    [areas]
  );
  const updateSelectedAreas = useCallback(
    (updatedAreas) => {
      setAreas(updatedAreas);
      if (uniqueView === 0) {
        setUniqueView(uniqueView + 1);
      }
    },
    [uniqueView]
  );
  const filteredGalleryData = photoTimeline
    .filter(({ location }) =>
      areas.find(
        ({ id, isSelected }) => isSelected && id === (location || {}).code
      )
    )
    .reduce((arr, { media }) => arr.concat(media), []);
  let allMediaItems;
  const galleryRenderData = filteredGalleryData.length
    ? filteredGalleryData
    : (allMediaItems = photoTimeline.reduce(
        (arr, { media }) => arr.concat(media),
        []
      ))
        .sort((a, b) => -Math.sign((a.rating || 0.5) - (b.rating || 0.5)))
        .slice(0, galleryMaxFeatured);
  const selectedAreas = areas.filter(({ isSelected }) => isSelected);

  return (
    <div>
      <div className={styles.heading}>Media by Country</div>
      <WorldMap
        areas={areas} // TODO: BUG, clicks on the map do not update URL (tagline)
        onSelect={updateSelected}
      />
      <TagLine
        className={styles.tagLine}
        tags={areas}
        onTagsChange={updateSelectedAreas}
      />
      <h1>
        {filteredGalleryData.length
          ? `Media from ${selectedAreas.map(({ name }) => name).join(", ")}`
          : "Featured Media"}
      </h1>
      <Gallery key={uniqueView} images={galleryRenderData} />
      {!filteredGalleryData.length &&
      allMediaItems.length > galleryRenderData.length ? (
        <p className={styles.center}>
          There are {allMediaItems.length - galleryRenderData.length} more media
          entries. Filter by country to see them!
        </p>
      ) : null}
    </div>
  );
};

export default WorldPhotoMap;
