import React, { useCallback, useState, useRef, useLayoutEffect } from "react";
import {
  getCurrentLocationReplacing,
  setCurrentUrlReplacing,
  getStringSlug,
  getUrlSearchKey,
  URI_KEY_FOR,
} from "../utils";
import styles from "./styles.module.scss";
import Icon from "../Icon";
import Gallery from "../Gallery";

const defaultIconsForTags = {
  travel: "travel",
  career: "handbag",
  education: "mortarboard",
  competitions: "trophy",
  "public speaking": "microphone",
  writing: "pen",
  teaching: "teacher",
  projects: "rocket",
};

const extractDate = (data) => new Date(data.date || data["date-start"]); // Must be UTC; otherwise date may not be the one you'd expect
const getPostUriComponent = (postData) => {
  const date = extractDate(postData);
  return `${getStringSlug(postData.title)}-${(date.getUTCMonth() + 1)
    .toString()
    .padStart(2, "0")}-${date.getUTCDate().toString().padStart(2, "0")}-${date
    .getUTCFullYear()
    .toString()
    .padStart(2, "0")}`;
};
const matchesTags = (filterTags, postTags) =>
  filterTags.length === 0
    ? true
    : filterTags
        .map((t) => postTags.indexOf(t) !== -1)
        .reduce((a, b) => a || b, false);

// Scrolls to component only once, globally, on the page open.
let globalFirstRender = true;

/**
 * @param {*|undefined} post - Post. When undefined, sets URL as there is no post opened.
 */
const setUrlToMatchPost = (post) =>
  setCurrentUrlReplacing({
    search: {
      [URI_KEY_FOR.TIMELINE]: post ? getPostUriComponent(post) : null,
    },
    searchTitles: {
      [URI_KEY_FOR.TIMELINE]: post ? post.title : null,
    },
    metadata: {
      postTags: post ? post.tags : null,
    },
  });

/**
 * @param {Object} props - Component's properties.
 * @param {{ date: Date, date-start: Date, date-end: Date, html: String, title: String, tags: String[] }[]} props.data - Timeline data array.
 * @returns {Component} - React component.
 */
const Timeline = ({ data, filter: { tags = [] } = {}, title = "Timeline" }) => {
  const openedPostRef = useRef();
  const urlDateMatch =
    (getUrlSearchKey(URI_KEY_FOR.TIMELINE) || "").match(
      /([0-9]{1,2})-([0-9]{1,2})-([0-9]{1,4})$/
    ) || [];
  const uriSelectedPostTime =
    urlDateMatch && urlDateMatch.length
      ? Date.UTC(urlDateMatch[3], urlDateMatch[1] - 1, urlDateMatch[2])
      : null;
  const sortedData = data
    .slice()
    .filter((a) => matchesTags(tags, a.tags || []))
    .sort(
      (a, b) =>
        -Math.sign(
          new Date(a.date || a["date-start"]).getTime() -
            new Date(b.date || b["date-start"]).getTime()
        )
    );
  const initiallyOpenedPostIndex = sortedData.findIndex(
    (post) => extractDate(post).getTime() === uriSelectedPostTime
  );
  const [state, setState] = useState({
    openedPost: initiallyOpenedPostIndex,
  });
  const isAnythingOpened = state.openedPost !== -1;
  if (globalFirstRender && initiallyOpenedPostIndex === -1) {
    globalFirstRender = false;
  }
  const titleClickHandler = useCallback(
    (event, postI) => {
      event.preventDefault(); // Link follow prevention
      const newlyOpenedPost = state.openedPost === postI ? -1 : postI;
      setUrlToMatchPost(sortedData[newlyOpenedPost] || undefined);
      setState({
        ...state,
        openedPost: newlyOpenedPost,
      });
    },
    [sortedData, state]
  );
  useLayoutEffect(() => {
    if (!globalFirstRender || !openedPostRef.current) {
      return;
    }
    const post = sortedData[state.openedPost];
    setUrlToMatchPost(post);
    openedPostRef.current.scrollIntoView();
    globalFirstRender = false;
  }, [state, sortedData]);
  return (
    <div className={styles.container}>
      {!!sortedData.length && (
        <div>
          <div className={styles.timelineHeader}>{title}</div>
          {!!tags.length && (
            <div className={styles.sub}>
              Includes only{" "}
              {tags.map((tag, i) => (
                <strong key={i}>#{tag} </strong>
              ))}
            </div>
          )}
        </div>
      )}
      {sortedData.reduce((arr, post, i, sortedData) => {
        const date = extractDate(post);
        const year = date.getFullYear();
        const isOpened = i === state.openedPost;
        const isMajor = post.importance > 3;
        const isSemiMajor = post.importance > 1;
        const HeaderTag = isAnythingOpened ? "div" : "a"; // For SEO. Removes unnessesary links
        return arr
          .concat(
            sortedData[i - 1] &&
              extractDate(sortedData[i - 1]).getFullYear() === year
              ? []
              : [
                  <div key={`y${year}`}>
                    <div className={styles.markerYear}>{year}</div>
                  </div>,
                ]
          )
          .concat([
            <div
              key={`d${date.toLocaleString("en-US", {
                month: "numeric",
                year: "numeric",
                day: "numeric",
              })}-${post.title}`}
              className={`${isMajor ? "" : styles.timelineItemMinor}${
                isSemiMajor && !isMajor
                  ? " " + styles.timelineItemSemiMajor
                  : ""
              }`}
            >
              <HeaderTag
                className={
                  styles.titleContainer +
                  (post.minor ? " " + styles.secondaryTitleContainer : "")
                }
                onClick={(e) => titleClickHandler(e, i)}
                href={
                  isAnythingOpened
                    ? null
                    : getCurrentLocationReplacing({
                        search: {
                          [URI_KEY_FOR.TIMELINE]: getPostUriComponent(post),
                        },
                      }).url
                }
                ref={isOpened ? openedPostRef : null}
              >
                <div className={styles.markerMonth}>
                  <div>
                    {date
                      .toLocaleString("en-US", { month: "short" })
                      .toUpperCase()}
                  </div>
                </div>
                <div className={styles.titleWrapper}>
                  {defaultIconsForTags[post.tags && post.tags[0]] && (
                    <Icon
                      image={defaultIconsForTags[post.tags && post.tags[0]]}
                      size={isMajor ? "s" : "xs"}
                    />
                  )}
                  <div>{post.title}</div>
                </div>
              </HeaderTag>
              <section
                className={`${styles.contentContainer} ${
                  isOpened ? styles.opened : styles.collapsed
                }`}
              >
                {!isOpened ? null : (
                  <div>
                    <h1>{post.title}</h1>
                    <div className={styles.contentContainerSubtitle}>
                      <div>
                        <Icon size="xs" image="clock" />
                        <span>
                          {post["date-end"]
                            ? `${new Date(
                                post["date-start"] || post["date"]
                              ).toLocaleDateString()} - ${new Date(
                                post["date-end"]
                              ).toLocaleDateString()}`
                            : date.toLocaleDateString()}
                        </span>
                      </div>
                      {post["location"] && (
                        <div>
                          <Icon size="xs" image="location" />
                          <span>
                            {post["location"].name ||
                              post["location"].code ||
                              post["location"]}
                          </span>
                        </div>
                      )}
                    </div>
                    <div dangerouslySetInnerHTML={{ __html: post.html }} />
                    {post.media && (
                      <Gallery images={post.media} postTags={post.tags} />
                    )}
                    {post.tags && post.tags.length && (
                      <div className={styles.contentContainerSubtitle}>
                        {post.tags.join(", ")}
                      </div>
                    )}
                  </div>
                )}
              </section>
            </div>,
          ]);
      }, [])}
    </div>
  );
};

export default Timeline;
