import React from "react";
import styles from "./styles.module.scss";

const LanguagesVisual = ({ languages = [] } = {}) => {
  return (
    <div className={styles.container}>
      <span>I speak </span>
      {languages.map((lang, i, arr) => (
        <span key={lang}>
          <span className={styles.name}>{lang}</span>
          {arr.length - 1 === i ? null : <span>, </span>}
        </span>
      ))}
    </div>
  );
};

export default LanguagesVisual;
