import React from "react";
import styles from "./styles.module.scss";

const GalleryFooter = ({
  currentView = {}, // TODO: no data when first opened by URL
  currentIndex,
  views,
}) => (
  <div className={styles.imageFooter}>
    {currentView.caption && (
      <h2 className={styles.imageTitle}>{currentView.caption}</h2>
    )}
    <div className={styles.sub}>
      <b>
        {currentIndex + 1}/{views.length}
      </b>
      {currentView.w &&
        currentView.h &&
        `, original size: ${currentView.w}x${currentView.h}`}
      {currentView.d &&
        `, date taken: ${new Date(currentView.d).toLocaleDateString()}`}
    </div>
  </div>
);

export default GalleryFooter;
