import React, { useState, useRef, useCallback, useEffect } from "react";
import Header from "../Header";
import { useInterval, useEventListener } from "../utils";
import styles from "./styles.module.scss";

const passiveEventOptions = { passive: true, capture: false };
const activeEventOptions = { passive: false, capture: false };

const updateParallaxElements = (scrollTop) => {
  const amount = Math.max(0, scrollTop / 2);
  requestAnimationFrame(() => {
    for (const element of document.querySelectorAll(".headParallax")) {
      element.style.transform = `translateY(${amount}px)`;
    }
  });
};

const Swiper = ({ slide = 0, children, onSlideChange }) => {
  const refWrapper = useRef(null);
  const [state, setState] = useState({
    slideNumber: +slide,
    transition: 0, // scale (-1..0..1)
    cancelTransition: false,
    startPosX: -1, // touch start pos
    startPosY: -1,
  });
  // console.log(`state.slideNumber=${state.slideNumber}, slide=${slide}`);
  if (+slide !== state.slideNumber && !state.transition) {
    updateParallaxElements(window.scrollY);
    setState({
      ...state,
      transition:
        Math.sign(slide - state.slideNumber) *
        0.01 *
        (Math.abs(slide - state.slideNumber) > 1 ? -1 : 1),
    });
  }
  const primaryChild = children[state.slideNumber % children.length];
  const additionalChild =
    children[
      (state.slideNumber + children.length + Math.sign(state.transition)) %
        children.length
    ];

  const styleA = {};
  const styleB = {};
  styleA.transform = `translate3d(${-state.transition * 100}%,0,0)`;
  styleB.transform = `translate3d(${
    (-state.transition + Math.sign(state.transition)) * 100
  }%,0,0)`;

  const onTouchMove = useCallback(
    (e) => {
      if (
        e.changedTouches.length === 0 ||
        state.cancelTransition ||
        !refWrapper ||
        !refWrapper.current ||
        window.scrollY >= window.innerHeight * 0.5
      ) {
        return;
      }
      const currentX = e.changedTouches[0].clientX;
      const currentY = e.changedTouches[0].clientY;
      const deltaX = currentX - state.startPosX;
      const deltaY = currentY - state.startPosY;
      const isVertical = Math.abs(deltaX) <= Math.abs(deltaY);
      if (state.startPosX === -1) {
        if (!isVertical && state.transition && e.cancelable) {
          e.preventDefault();
          return true;
        }
        return setState({
          ...state,
          startPosX:
            currentX + refWrapper.current.offsetWidth * state.transition,
          startPosY: currentY,
        });
      }
      if (isVertical && !state.transition) {
        return setState({
          ...state,
          cancelTransition: true,
        });
      }
      if (state.transition === 0) {
        updateParallaxElements(window.scrollY);
      }
      setState({
        ...state,
        transition: -deltaX / refWrapper.current.offsetWidth,
      });
      if (e.cancelable) {
        e.preventDefault();
      }
    },
    [state]
  );
  useEventListener(refWrapper, "touchmove", onTouchMove, activeEventOptions);

  const onTouchEnd = useCallback(
    (e) => {
      setState({
        ...state,
        cancelTransition: false,
        startPosX: -1,
        startPosY: -1,
      });
    },
    [state]
  );
  useEventListener(refWrapper, "touchend", onTouchEnd, passiveEventOptions);

  const onScroll = useCallback(() => {
    updateParallaxElements(window.scrollY);
  }, []);
  useEventListener(refWrapper, "scroll", onScroll, passiveEventOptions);

  useInterval(() => {
    const isInManualTransition = state.startPosX !== -1;
    if (isInManualTransition || !state.transition) {
      return;
    }
    const smoothTransitionHack = state.transition; // The page tooks ~200ms to re-render on the end!
    // It makes a strange effect when it's not scrolled to the end and re-renders
    let nextTransition =
      state.transition +
      Math.sign(state.transition) *
        Math.max(0.005, (1 - Math.abs(state.transition)) / 6);
    nextTransition = Math.min(1, Math.max(-1, nextTransition));
    if (smoothTransitionHack >= 1 || smoothTransitionHack <= -1) {
      const nextSlideNumber =
        (children.length + state.slideNumber + Math.sign(nextTransition)) %
        children.length;
      if (typeof onSlideChange === "function") {
        onSlideChange(nextSlideNumber);
      }
      setState({
        ...state,
        slideNumber: nextSlideNumber,
        transition: 0,
      });
    } else {
      setState({
        ...state,
        transition: nextTransition,
      });
    }
  }, 1000 / 60);

  useEffect(() => {
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  });

  const nextSlideNumber =
    (children.length + state.slideNumber + Math.sign(state.transition)) %
    children.length;

  return (
    <div className={styles.swipeWrapper} ref={refWrapper}>
      <Header
        transition={state.transition}
        slideNumber={state.slideNumber}
        nextSlideNumber={nextSlideNumber}
      />
      <div
        key={state.slideNumber}
        className={styles.contentContainer}
        style={styleA}
      >
        {primaryChild}
      </div>
      {state.transition === 0 ? null : (
        <div
          key={nextSlideNumber}
          className={styles.contentContainer}
          style={styleB}
        >
          {additionalChild}
        </div>
      )}
    </div>
  );
};

export default Swiper;
